
export default {
    hasFourConsecutiveNumbers: function(combo){ 
        const sorted = combo.slice().sort((a, b) => a - b);
        let consecutiveCount = 1;
    
        for (let i = 1; i < sorted.length; i++) {
            if (sorted[i] === sorted[i - 1] + 1) {
                consecutiveCount++;
                if (consecutiveCount >= 4) {
                    return true;
                }
            } else {
                consecutiveCount = 1;
            }
        }
        return false;
    },

    combinations: function(arr, n) {
        const result = [];
        function combine(start, combo) {
            if (combo.length === n) {
                result.push([...combo]);
                return;
            }
            for (let i = start; i < arr.length; i++) {
                combo.push(arr[i]);
                combine(i + 1, combo);
                combo.pop();
            }
        }
        combine(0, []);
        return result;
    },

    generateWheelingSystem: function(numbers, drawSize, M, N) {
        const targetCombos = this.combinations(numbers, M);
        const result = new Set();
        const allCombinations = this.combinations(numbers, drawSize).filter(combo => !this.hasFourConsecutiveNumbers(combo)); 
    
        while (targetCombos.length > 0) {
            let bestCombo = null;
            let bestCovered = [];

            allCombinations.forEach(combo => {
                const covered = targetCombos.filter(targetCombo =>
                    targetCombo.filter(num => combo.includes(num)).length >= N
                );

                if (covered.length > bestCovered.length) {
                    bestCombo = combo;
                    bestCovered = covered;
                }
            });

            if (!bestCombo) {
                throw new Error("参数可能有误。");
            }

            result.add(bestCombo.join(","));

            bestCovered.forEach(coveredCombo => {
                const index = targetCombos.indexOf(coveredCombo);
                if (index > -1) {
                    targetCombos.splice(index, 1);
                }
            });

            const index = allCombinations.indexOf(bestCombo);
            if (index > -1) {
                allCombinations.splice(index, 1);
            }
        }
        let tmp = Array.from(result).map(combo => combo.split(",").map(Number));
        let resultStr = [];
        for(let i = 0; i < tmp.length; i++) {
            let item = tmp[i];
            // let str = item.join(',');
            resultStr.push(item);
        }

        return resultStr;
    }
}
  


